  padding: 5px;
  text-align: center;
  color: #5a5a5a;
  margin-bottom: 10px;
}

.slick-card .video-actions {
  margin-bottom:  0px;
  border-radius: 0px 0px 5px 5px;
}



.mp3-card {
  margin: 5px 10px;
  border: 1px solid #eee;
}
.mp3-info{

}
.mp3-thumb {
