  padding: 5px;
}
.mp3-actions ul {
      margin-bottom: 0px;
  list-style: none;
    -webkit-padding-start: 0px;
    -moz-padding-start: 0px;
    text-align: center;
  // width: 100px;
  // display: inline-block;
  // height: 50px;
  // float: right;
  // padding:0px 5px 0px 0px;
}
.mp3-actions ul li {
  display: inline-block;
  margin: 0 5px;
}



